import React from "react";
import { useSearchParams } from "react-router-dom";
import {
  Pagination,
  PaginationContent,
  PaginationEllipsis,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from "@/components/ui/pagination";
import { ChevronLeft, ChevronRight, MoreHorizontal, Zap, Brain, Sparkles } from "lucide-react";

interface CustomPaginationProps {
  currentPage: number;
  totalPages: number;
  onPageChange: (page: number) => void;
  totalItems?: number;
  itemsPerPage?: number;
}

const CustomPagination: React.FC<CustomPaginationProps> = ({
  currentPage,
  totalPages,
  onPageChange,
  totalItems,
  itemsPerPage = 25,
}) => {
  const [searchParams, setSearchParams] = useSearchParams();

  const pageNumbers = [];

  // Decide how many page numbers to show
  const maxPageNumbersToShow = 7;

  let startPage = Math.max(
    1,
    currentPage - Math.floor(maxPageNumbersToShow / 2)
  );
  let endPage = startPage + maxPageNumbersToShow - 1;

  if (endPage > totalPages) {
    endPage = totalPages;
    startPage = Math.max(1, endPage - maxPageNumbersToShow + 1);
  }

  // Generate the list of page numbers
  for (let i = startPage; i <= endPage; i++) {
    pageNumbers.push(i);
  }

  // Calculate showing range
  const startItem = (currentPage - 1) * itemsPerPage + 1;
  const endItem = Math.min(currentPage * itemsPerPage, totalItems || totalPages * itemsPerPage);
  const totalDisplayItems = totalItems || totalPages * itemsPerPage;

  const handlePageChange = (page: number) => {
    onPageChange(page);
    setSearchParams({ page: page.toString() });
  };

  return (
    <div className="flex items-center justify-between my-8 px-4 py-6 bg-gradient-to-r from-slate-50/50 via-blue-50/30 to-purple-50/50 rounded-xl border border-slate-200/60 backdrop-blur-sm shadow-sm">
      {/* Left side - Items info with AI theme */}
      <div className="flex items-center space-x-3">
        <div className="flex items-center justify-center w-8 h-8 rounded-lg bg-gradient-to-br from-blue-500 to-purple-600 shadow-sm">
          <Brain className="h-4 w-4 text-white" />
        </div>
        <div className="text-sm font-medium text-slate-700">
          Showing <span className="text-blue-600 font-semibold">{startItem}</span> to{" "}
          <span className="text-blue-600 font-semibold">{endItem}</span> of{" "}
          <span className="text-slate-900 font-semibold">{totalDisplayItems}</span> results
        </div>
      </div>

      {/* Right side - Pagination controls */}
      <div className="flex items-center space-x-2">
        {/* Previous Button */}
        <button
          onClick={() => currentPage > 1 && handlePageChange(currentPage - 1)}
          disabled={currentPage <= 1}
          className={`
            flex items-center space-x-2 px-4 py-2.5 rounded-lg text-sm font-medium transition-all duration-200
            ${currentPage <= 1
              ? 'bg-slate-100 text-slate-400 cursor-not-allowed'
              : 'bg-white text-slate-700 hover:bg-gradient-to-r hover:from-blue-50 hover:to-purple-50 hover:text-blue-700 border border-slate-200 hover:border-blue-300 shadow-sm hover:shadow-md'
            }
          `}
        >
          <ChevronLeft className="h-4 w-4" />
          <span>Previous</span>
        </button>

        {/* Page Numbers */}
        <div className="flex items-center space-x-1">
          {/* First page if not in range */}
          {startPage > 1 && (
            <>
              <button
                onClick={() => handlePageChange(1)}
                className="w-10 h-10 rounded-lg text-sm font-medium transition-all duration-200 bg-white text-slate-700 hover:bg-gradient-to-br hover:from-blue-500 hover:to-purple-600 hover:text-white border border-slate-200 hover:border-transparent shadow-sm hover:shadow-md"
              >
                1
              </button>
              {startPage > 2 && (
                <div className="flex items-center justify-center w-10 h-10">
                  <MoreHorizontal className="h-4 w-4 text-slate-400" />
                </div>
              )}
            </>
          )}

          {/* Page number buttons */}
          {pageNumbers.map((pageNumber) => (
            <button
              key={pageNumber}
              onClick={() => handlePageChange(pageNumber)}
              className={`
                w-10 h-10 rounded-lg text-sm font-medium transition-all duration-200 shadow-sm
                ${pageNumber === currentPage
                  ? 'bg-gradient-to-br from-blue-500 to-purple-600 text-white shadow-lg shadow-blue-500/25 border-0 transform scale-105'
                  : 'bg-white text-slate-700 hover:bg-gradient-to-br hover:from-blue-500 hover:to-purple-600 hover:text-white border border-slate-200 hover:border-transparent hover:shadow-md'
                }
              `}
            >
              {pageNumber}
            </button>
          ))}

          {/* Last page if not in range */}
          {endPage < totalPages && (
            <>
              {endPage < totalPages - 1 && (
                <div className="flex items-center justify-center w-10 h-10">
                  <MoreHorizontal className="h-4 w-4 text-slate-400" />
                </div>
              )}
              <button
                onClick={() => handlePageChange(totalPages)}
                className="w-10 h-10 rounded-lg text-sm font-medium transition-all duration-200 bg-white text-slate-700 hover:bg-gradient-to-br hover:from-blue-500 hover:to-purple-600 hover:text-white border border-slate-200 hover:border-transparent shadow-sm hover:shadow-md"
              >
                {totalPages}
              </button>
            </>
          )}
        </div>

        {/* Next Button */}
        <button
          onClick={() => currentPage < totalPages && handlePageChange(currentPage + 1)}
          disabled={currentPage >= totalPages}
          className={`
            flex items-center space-x-2 px-4 py-2.5 rounded-lg text-sm font-medium transition-all duration-200
            ${currentPage >= totalPages
              ? 'bg-slate-100 text-slate-400 cursor-not-allowed'
              : 'bg-white text-slate-700 hover:bg-gradient-to-r hover:from-blue-50 hover:to-purple-50 hover:text-blue-700 border border-slate-200 hover:border-blue-300 shadow-sm hover:shadow-md'
            }
          `}
        >
          <span>Next</span>
          <ChevronRight className="h-4 w-4" />
        </button>

        {/* AI-themed indicator */}
        <div className="flex items-center justify-center w-10 h-10 rounded-lg bg-gradient-to-br from-emerald-500 to-teal-600 shadow-sm ml-2">
          <Sparkles className="h-4 w-4 text-white animate-pulse" />
        </div>
      </div>
    </div>
  );
};

export default CustomPagination;
